import React from 'react'
import AceEditor from 'react-ace'
import {NotificationContainer, NotificationManager} from 'react-notifications'
import "ace-builds/src-noconflict/mode-json"
import "ace-builds/src-noconflict/snippets/json"
import "ace-builds/src-noconflict/theme-github"
import "react-notifications/lib/notifications.css"

export default class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            formattype: 'copy',
            isLive: true
        }
    }

    uploadSource = (event) => {
        const reader = new FileReader();

        reader.onabort = () => NotificationManager.error('File reading was aborted');
        reader.onerror = () => NotificationManager.error('File reading was failed');
        reader.onload = () => {
            const content = reader.result;
            console.log('Content = ' + content);
            this.setState({sourceValue: content}, () => {
                this.format()
            });
        };
        reader.readAsText(event.target.files[0]);
    };

    clear = () => {
        this.setState({sourceValue: ''}, () => {
            this.format()
        })
    };

    changeLive = () => {
        this.setState(prevState => ({
            isLive: !prevState.isLive
        }), () => {
            if (this.state.isLive) {
                this.format();
            }
        })
    };

    downloadTarget = () => {
        const text = this.state.targetValue;
        const element = document.createElement("a");
        const file = new Blob([text], {type: 'application/json'});
        element.href = URL.createObjectURL(file);
        element.download = "formattedjson.json";
        document.body.appendChild(element);
        element.click();
    };

    copyTarget = () => {
        const text = this.state.targetValue;
        navigator.clipboard.writeText(text);
        NotificationManager.success('Copied to clipboard.');
    };

    minify = () => {
        this.setState({formattype: 'minify'}, () => {
            this.format()
        })
    };

    beautify = () => {
        this.setState({formattype: 'beautify'}, () => {
            this.format()
        })
    };

    format = () => {
        const sourceText = this.state.sourceValue;
        if (this.state.formattype === 'copy') {
            this.setState({targetValue: sourceText});
            return
        }

        let xhr = new XMLHttpRequest();
        xhr.open('POST', 'localhost:8080/' + this.state.formattype, true);
        xhr.setRequestHeader('Content-type', 'application/json');

        xhr.onreadystatechange = () => {
            if (xhr.readyState === 4 && xhr.status === 200) {
                this.setState({targetValue: xhr.responseText})
            }
        };
        xhr.send(sourceText);
    };


    onSourceChange = (newValue) => {
        this.setState({sourceValue: newValue});
        if (this.state.isLive) {
            this.format()
        }
    };

    onTargetChange = (newValue) => {
        this.setState({targetValue: newValue});
    };

    render = () => {
        return (
            <div className="product-container">
                <div className="editor-container">
                    <div className="editor-content">
                        <div className="editor-toolbar">
                            <button type="button" className="btn btn-link trigger">
                                <span>Upload (.json/.txt)</span>
                                <input type="file" onChange={this.uploadSource} autoComplete="off"
                                       accept=".txt, .json, text/plain, application/json"
                                       className="form-control file-element"/>
                            </button>
                            <button type="button" onClick={this.clear} className="btn btn-link">Clear</button>
                            <button id="islivebtn" type="button" onClick={this.changeLive} className="btn btn-link">
                                <input type="checkbox" checked={this.state.isLive} readOnly/>
                                <span>Live format</span>
                            </button>
                        </div>
                        <AceEditor
                            placeholder="Insert your json here"
                            style={{width: '100%', height: 'calc(100vh - 220px)', fontSize: '14px'}}
                            mode="json"
                            theme="github"
                            onChange={this.onSourceChange}
                            value={this.state.sourceValue}
                            setOptions={{useWorker: false}}
                            editorProps={{$blockScrolling: true}}
                        />
                    </div>
                    <div className="editor-content">
                        <div className="editor-toolbar">
                            <button type="button" onClick={this.downloadTarget} className="btn btn-link">Download
                            </button>
                            <button type="button" onClick={this.copyTarget} className="btn btn-link">Copy</button>
                            <button type="button" onClick={this.minify} className="btn btn-link">Minify</button>
                            <button type="button" onClick={this.beautify} className="btn btn-link">Beautify</button>
                        </div>
                        <AceEditor
                            style={{width: '100%', height: 'calc(100vh - 220px)', fontSize: '14px'}}
                            mode="json"
                            theme="github"
                            onChange={this.onTargetChange}
                            value={this.state.targetValue}
                            setOptions={{useWorker: false}}
                            editorProps={{$blockScrolling: true}}/>
                        <NotificationContainer/>
                    </div>
                </div>
            </div>
        );
    }
}
