import React from 'react'

const About = () => {
    return (
        <main class="page-main-content">
            <div className="container" style={{ width: '100%', height: 'calc(100vh - 250px)', 'font-size': '14px' }} >
                <h1 className="page-header-title">Authors</h1>
                <section className="page-body">
                <ul>
                    <li>Adam Stelmaszyk</li>
                    <li>Mateusz Bąk</li>
                    <li>Mikołaj Sienkiewicz</li>
                    <li>Vladyslav Hlushchenko</li>
                </ul>
                </section>
            </div>
        </main>
    )
};

export default About